@import '../src//styles/reset.scss';
@import '../src/styles/typography.scss';
@import '../src/styles/layouts.scss';
@import '../src/styles/colors.scss';

#root {
  position: relative;
  // height: 100vh;
  // min-height: 100vh;
  // min-height: -webkit-fill-available;
  width: 100%;
  height: 100%;
  min-height: 100%;
}
.App {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  @media (min-width: $xl) {
    &::-webkit-scrollbar {
      width: 0.5rem;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &::-webkit-scrollbar-thumb {
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid transparent;
      border-radius: 2rem;
    }
    ::-webkit-scrollbar-thumb:hover {
      background: rgba(255, 255, 255, 0.2);
    }
  }
}

.container {
  max-width: 100%;
  padding: 0 $unit;
  margin-left: auto;
  margin-right: auto;
  @media (min-width: $md) {
    max-width: 600px;
  }
  @media (min-width: $lg) {
    max-width: 768px;
  }
  @media (min-width: $xl) {
    max-width: 992px;
  }
  @media (min-width: $xxl) {
    max-width: 1200px;
  }
}

.underline {
  width: calc(4 *#{$unit});
  height: calc(0.25 *#{$unit});
  background-color: $secondaryColor;
  border: 3px solid $secondaryColor;
  border-radius: 4px;
}

.s-shape-up,
.s-shape-down {
  img {
    width: calc(1.618 * 100vw);
    height: auto;
    @media (orientation: landscape) and (min-width: $md) {
      width: calc(1.618 * 50vw);
    }
  }
}

[data-theme='light'] {
  .s-shape-up,
  .s-shape-down {
    img {
      opacity: 0.04;
    }
  }
}

[data-theme='dark'] {
  .s-shape-up,
  .s-shape-down {
    img {
      opacity: 0.01;
    }
  }
}
